import React from "react";
import HeaderProject from "./HeaderProject";
import BackButton from "./BackButton";
import Footer from "./Footer";

const Success = ({id}) => {
    return (
        <>
            <div className='success' id={id}>
                <HeaderProject id='headerProject'/>
                <div className='success-message'>
                    <p>
                        Hi, looks like you have my legacy Portfolio link.
                    </p>
                    <p>
                        Sorry for that but I had to change it.
                    </p>
                    <p>
                        <span>
                            Please use below link - it will redirect you to the correct website.
                        </span>
                    </p>
                    <p>
                        Hope you will enjoy it :)
                    </p>
                </div>
                <BackButton/>
                <Footer id='footer'/>
            </div>
        </>
    )
};

export default Success;